import React from 'react'
import {createHashRouter} from 'react-router-dom'
import Home from '@/page/Home'
import WordPage from '@/page/WordDetail'

const router = createHashRouter([
    {
        path: "/",
        element: <Home/>,
    },
    {
        path:"/word",
        element:<WordPage/>
    }
]);

export default router

