import React, {useEffect, useRef, useState} from 'react';
import styles from './style.module.scss'
import {useNavigate} from "react-router-dom";
import {Card, Collapse, Space} from "antd-mobile";
import data from '@/constant/wordList'

const Index = () => {
    const navigate = useNavigate()
    const scrollRef = useRef()
    // const [activeItem,setActiveItem] = useState('')
    // const [activeParentItem,setParentActiveItem] = useState('')
    const activeItem = sessionStorage.getItem('activeItem') || ''
    const activeParentItem = sessionStorage.getItem('activeParentItem') || ''

    const handleClick = (val: any,parentTitle:string) => {
        navigate('/word', {state: val})
        // @ts-ignore
        sessionStorage.setItem('scrollPosition',scrollRef.current.scrollTop)
        sessionStorage.setItem('activeItem',val.title)
        sessionStorage.setItem('activeParentItem',parentTitle)
    }

    useEffect(() => {
        // @ts-ignore
        scrollRef.current.scrollTop = sessionStorage.getItem('scrollPosition') || 0
        // @ts-ignore
        // setActiveItem(sessionStorage.getItem('activeItem') || '')
    }, []);

    return (
        // @ts-ignore
        <div className={styles.root} ref={scrollRef} >
            <div className="header">
                5500
            </div>
            <Collapse accordion={true} defaultActiveKey={activeParentItem} className="collapse">
                {
                    data.map((item,index)=>{
                        return(
                            <Collapse.Panel key={item.title} title={item.title}>
                                <Space className="space" direction="vertical">
                                    {
                                        item.data.map((_item: { title: string, video: string }, _index: number) => {
                                            return <Card onClick={() => {
                                                handleClick(_item,item.title)
                                            }} className={activeItem===_item.title?'card card--active':'card'} key={'b'+_index}>{_item.title}</Card>
                                        })
                                    }
                                </Space>
                            </Collapse.Panel>
                        )
                    })
                }

            </Collapse>

        </div>
    );
};

export default Index;
